*/
.footer {
  background-color: #1a1a1a;
  color: #e0e0e0;
  border-top: 1px solid #333;
}

.footerContent {
  max-width: 1200px;
  margin: 0 auto;
  padding: 60px 20px 40px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 40px;
}

/* ë¸Œëœë“œ ì„¹ì…˜ */
.footerBrand {
  grid-column: 1;
}

.footerTitle {
  font-size: 24px;
  font-weight: 700;
  margin-bottom: 12px;
  color: #fff;
}

.footerDescription {
  font-size: 14px;
  line-height: 1.6;
  color: #b0b0b0;
  margin: 0;
}

/* ë§í¬ ì„¹ì…˜ */
.footerLinks {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 30px;
  grid-column: 2;
}

.footerColumn a {
  color: #b0b0b0;
  text-decoration: none;
  font-size: 14px;
  transition: color 0.3s ease;
  display: inline-block;
  white-space: nowrap;      /* âœ… ì¤„ë°”ê¿ˆ ì ˆëŒ€ ê¸ˆì§€ */
  word-break: keep-all;     /* âœ… ë‹¨ì–´ ì¤‘ê°„ ì¤„ë°”ê¿ˆ ë°©ì§€ */
  overflow-wrap: normal;    /* âœ… ìë™ ì¤„ë°”ê¿ˆ ë°©ì§€ */
}

.footerSubtitle {
  font-size: 14px;
  font-weight: 600;
  text-transform: uppercase;
  color: #fff;
  margin-bottom: 16px;
  letter-spacing: 0.5px;
}

.footerColumn ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.footerColumn li {
  margin-bottom: 10px;
}

.footerColumn a {
  color: #b0b0b0;
  text-decoration: none;
  font-size: 14px;
  transition: color 0.3s ease;
}

.footerColumn a:hover {
  color: #fff;
}

/* API ì •ë³´ ì„¹ì…˜ */
.footerApi {
  grid-column: 3;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.footerCredit {
  font-size: 14px;
  line-height: 1.6;
  color: #b0b0b0;
  margin: 0;
}

.footerCredit a {
  color: #fff;
  text-decoration: none;
  font-weight: 500;
  transition: color 0.3s ease;
}

.footerCredit a:hover {
  color: #ff6b6b;
}

/* í•˜ë‹¨ ì €ì‘ê¶Œ */
.footerBottom {
  background-color: #0f0f0f;
  padding: 20px;
  text-align: center;
  border-top: 1px solid #333;
}

.footerCopyright {
  margin: 0;
  font-size: 13px;
  color: #808080;
}

/* íƒœë¸”ë¦¿ ë°˜ì‘í˜• */
@media (max-width: 768px) {
  .footerContent {
    grid-template-columns: 1fr;
    gap: 30px;
    padding: 40px 20px 30px;
  }

  .footerBrand {
    grid-column: auto;
  }

  .footerLinks {
    grid-column: auto;
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;
  }

  .footerApi {
    grid-column: auto;
  }
}

/* ëª¨ë°”ì¼ ë°˜ì‘í˜• */
@media (max-width: 480px) {
  .footerContent {
    grid-template-columns: 1fr;
    gap: 20px;
    padding: 30px 15px 20px;
  }

  .footerLinks {
    grid-template-columns: 1fr;
    gap: 15px;
  }

  .footerTitle {
    font-size: 20px;
  }

  .footerSubtitle {
    font-size: 12px;
    margin-bottom: 12px;
  }


  .footerCopyright {
    font-size: 12px;
  }
  
 

  
}